/** å–è·¯å¾„æœ€åä¸€æ®µï¼ša/b/c -> c */
export function lastSegment(path: string) {
  return (path || '').split('/').filter(Boolean).pop() || path || '';
}

/* ------------------------------------------------------------------ */
/*  ä»»åŠ¡è¡Œï¼šæ¨¡æ¿æ¸²æŸ“                                                   */
/* ------------------------------------------------------------------ */
export function makeTaskLine(opts: {
  themePath?: string;
  template?: string;                         // ä¾‹å¦‚ "{{ä»»åŠ¡çŠ¶æ€}} {{icon}} {{å†…å®¹}} ..."
  fields: Record<string, string | number | boolean | null | undefined>;
}): string {
  const { template = '{{å‰ç¼€}}{{å†…å®¹}}', fields } = opts;

  /** å­—æ®µå®‰å…¨åŒ– */
  const safe: Record<string, string> = {};
  Object.entries(fields || {}).forEach(([k, v]) => { safe[k] = v == null ? '' : String(v); });
  if (!('å‰ç¼€' in safe))      safe['å‰ç¼€'] = '';
  if (!('ä»»åŠ¡å‰ç¼€' in safe))  safe['ä»»åŠ¡å‰ç¼€'] = safe['å‰ç¼€'];

  /** æ›¿æ¢å ä½ç¬¦ï¼šæ”¯æŒ {{key}} å’Œ {{@key}} */
  const PLACEHOLDER = /\{\{\s*@?([^}]+?)\s*\}\}/g;
  let line = template.replace(PLACEHOLDER, (_m, p1) => {
    const key = String(p1).trim();
    return key in safe ? safe[key] : '';
  });

  /** è‹¥æ¨¡æ¿æ²¡æœ‰ä»»ä½•å‰ç¼€å ä½ç¬¦ï¼Œåˆ™åœ¨æœ€å‰é¢è¡¥ä¸€ä¸ª */
  const hasPrefixSlot = /\{\{\s*@?(?:å‰ç¼€|ä»»åŠ¡å‰ç¼€)\s*\}\}/.test(template);
  if (!hasPrefixSlot && !line.startsWith('- [')) {
    const fallback = safe['å‰ç¼€'] || '- [ ] ';
    line = fallback + line;
  }

  /** å‹ç¼©å¤šä½™ç©ºæ ¼å¹¶è¿”å› */
  return line.replace(/[ \t]+/g, ' ').trim();
}

/* ------------------------------------------------------------------ */
/*  å—ï¼šè®¡åˆ’ / æ€»ç»“ / æ€è€ƒ / æ‰“å¡                                       */
/* ------------------------------------------------------------------ */
export function makeBlock(opts: {
  category: string;                          // è®¡åˆ’ / æ€»ç»“ / æ€è€ƒ / æ‰“å¡
  dateISO?: string;
  themeLabel?: string;                       // ä¸»é¢˜:: é¡¶å±‚æ˜¾ç¤º
  icon?: string;
  content: string;                           // å¯å¤šè¡Œ
  tags?: string[];                           // æ ‡ç­¾:: a, b
  extra?: Record<string,string>;             // å‘¨æœŸ / åˆ†ç±» ...
  fieldsOrder?: string[];                    // è‡ªå®šä¹‰å­—æ®µé¡ºåº
}) {
  /* é»˜è®¤é¡ºåºï¼›å¯é€šè¿‡ fieldsOrder è¦†ç›– */
  const order = opts.fieldsOrder ?? ['åˆ†ç±»','æ—¥æœŸ','ä¸»é¢˜','å›¾æ ‡','æ ‡ç­¾','å‘¨æœŸ','åˆ†ç±»','å†…å®¹'];

  /** ç»„è£…æ‰€æœ‰å¯èƒ½å‡ºç°çš„å­—æ®µ */
  const dict: Record<string,string> = {
    'åˆ†ç±»': opts.category,
    'æ—¥æœŸ': opts.dateISO ? normalizeDateStr(opts.dateISO) : '',
    'ä¸»é¢˜': opts.themeLabel ?? '',
    'å›¾æ ‡': opts.icon ?? '',
    'æ ‡ç­¾': opts.tags?.join(', ') ?? '',
    ...opts.extra,                            // å‘¨æœŸ / åˆ†ç±» ç­‰
    'å†…å®¹': (opts.content || '').trim()
  };

  /** helper */
  const kv = (k:string,v?:string)=> v ? `${k}:: ${v}` : '';

  const lines: string[] = ['<!-- start -->'];

  /* æŒ‰é¡ºåºè¾“å‡º */
  order.forEach(f => {
    if (f==='å†…å®¹' && dict['å†…å®¹'].includes('\n')) {
      const [first,...rest] = dict['å†…å®¹'].split('\n');
      lines.push('å†…å®¹:: '+ first, ...rest);
    } else {
      const line = kv(f, dict[f]);
      if (line) lines.push(line);
    }
  });

  lines.push('<!-- end -->');
  return lines.join('\n');
}

/* ------------------------------------------------------------------ */
/*  æ—§ç‰ˆä»»åŠ¡è¡Œï¼ˆå¸¦ emoji & #æ ‡ç­¾ï¼‰                                     */
/* ------------------------------------------------------------------ */
export function makeTaskLineLegacy(opts: {
  prefix: '- [ ] ' | '- [x] ' | '- [-] ' | '';
  title: string;
  themePath?: string;
  icon?: string;
  due?: string;
  start?: string;
  scheduled?: string;
  createdISO?: string;
  timeHHMM?: string;
  duration?: string | number;
  repeat?: 'day' | 'week' | 'month' | 'year' | null;
  markDate?: string;
  statusEmoji?: string;
}) {
  const tagStr  = opts.themePath ? ` #${opts.themePath}` : '';
  const iconStr = opts.icon ? `${opts.icon} ` : '';
  const pieces: string[] = [];

  pieces.push(`${opts.prefix}${iconStr}${opts.title}${tagStr}`.trim());

  if (opts.timeHHMM) pieces.push(`(æ—¶é—´::${opts.timeHHMM})`);
  if (opts.duration !== undefined && opts.duration !== '')
    pieces.push(`(æ—¶é•¿::${String(opts.duration)})`);

  const addDate = (emoji: string, d?: string) => {
    if (d) pieces.push(`${emoji} ${normalizeDateStr(d)}`);
  };
  addDate(EMOJI.created,   opts.createdISO);
  addDate(EMOJI.start,     opts.start);
  addDate(EMOJI.scheduled, opts.scheduled);
  addDate(EMOJI.due,       opts.due);

  if (opts.repeat)
    pieces.push(`${EMOJI.repeat} every ${opts.repeat}${opts.repeat === 'day' ? '' : 's'}`);

  if (opts.statusEmoji && opts.markDate)
    pieces.push(`${opts.statusEmoji} ${normalizeDateStr(opts.markDate)}`);

  return pieces.join(' ').trim();
}