import { h, JSX } from 'preact';
import { Item, readField } from '@core/domain/schema';
import { getCategoryColor } from '@core/domain/categoryColorMap';
import { DataStore } from '@core/services/dataStore';
import { makeObsUri } from '@core/utils/obsidian';
import { TaskCheckbox } from '@shared/components/TaskCheckbox';   // â˜… æ–°å¢

interface BlockViewProps {
  items: Item[];
  groupField?: string;
  showMeta?: boolean;
  fields?: string[];
}

export function BlockView(props: BlockViewProps) {
  const { groupField, showMeta = true, fields } = props;
  let items = props.items;

  /* ------------------------- åˆ†ç»„é€»è¾‘ä¿æŒä¸å˜ ------------------------- */
  let grouped: Record<string, Item[]> | null = null;
  let groupKeys: string[] = [];
  if (groupField) {
    grouped = {};
    for (const it of items) {
      const key = String(readField(it, groupField) ?? '(none)');
      if (!grouped[key]) {
        grouped[key] = [];
        groupKeys.push(key);
      }
      grouped[key].push(it);
    }
    groupKeys.sort();
  }

  /* ------------------------- æ¸²æŸ“ä»»åŠ¡ / Block ------------------------- */

  const renderTaskLine = (item: Item) => {
    const isDone = item.status === 'done';
    return (
      <div style="margin-bottom:4px;line-height:1.5;">
        <TaskCheckbox
          done={isDone}
          onMarkDone={() => DataStore.instance.markItemDone(item.id)}
        />
        <a
          href={makeObsUri(item.id)}
          target="_blank"
          rel="noopener"
          class={isDone ? 'task-done' : ''}
        >
          {item.title}
        </a>
        {item.icon && <span class="tag-pill">{item.icon}</span>}
        {item.tags.map(t => (
          <span class="tag-pill" key={t}>
            {t}
          </span>
        ))}
      </div>
    );
  };

  const renderBlock = (item: Item) => (
    <div
      style="
        display:grid;
        grid-template-columns:auto minmax(180px,1fr);
        align-items:start;
        gap:8px;
        margin-bottom:8px;
      "
    >
      {showMeta && (
        <div style="font-size:90%;display:flex;flex-wrap:wrap;gap:4px;">
          <span
            class="tag-pill"
            style={`background:${getCategoryColor(item.category)};`}
          >
            {item.category}
          </span>
          {item.date && <span class="tag-pill">{item.date}</span>}
          {item.extra['å›¾æ ‡'] && (
            <span class="tag-pill">{item.extra['å›¾æ ‡']}</span>
          )}
        </div>
      )}
      <div style="white-space:pre-wrap;line-height:1.5;">
        <a href={makeObsUri(item.id)} target="_blank" rel="noopener">
          {item.content}
        </a>
      </div>
    </div>
  );

  const renderItem = (it: Item) =>
    it.type === 'task' ? renderTaskLine(it) : renderBlock(it);

  /* ------------------------- è¾“å‡º ------------------------- */
  return (
    <div>
      {grouped
        ? groupKeys.map(grp => (
            <div key={grp}>
              <h5>{grp}</h5>
              {grouped![grp].map(renderItem)}
            </div>
          ))
        : items.map(renderItem)}
    </div>
  );
}